      title.textContent = attributes.title;
      ChartScatterDirective.titleIdCounter++;
      title.setAttribute("id", `chart-scatter-${ChartScatterDirective.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `chart-scatter-${ChartScatterDirective.titleIdCounter}`);
